
import { defineComponent, PropType, reactive, ref } from 'vue'
import type { FormInstance, FormRules } from 'element-plus'
import { ICurrencyForm } from '@/types'

export interface IData {
  model: ICurrencyForm;
  isValid: boolean;
}

export default defineComponent({
  name: 'CurrencyForm',

  setup () {
    return {
      form: ref<FormInstance>(),
      rules: reactive<FormRules>({
        name: [
          { required: true, message: 'Обязательное поле', trigger: 'blur' },
          { max: 32, message: 'Максимальная длина 32 символа', trigger: 'blur' }
        ],
        code: [
          { required: true, message: 'Обязательное поле', trigger: 'blur' },
          { max: 3, message: 'Максимальная длина 3 символа', trigger: 'blur' }
        ],
        symbolLeft: [
          { required: true, message: 'Обязательное поле', trigger: 'blur' },
          { max: 12, message: 'Максимальная длина 12 символа', trigger: 'blur' }
        ],
        symbolRight: [
          { required: true, message: 'Обязательное поле', trigger: 'blur' },
          { max: 12, message: 'Максимальная длина 12 символа', trigger: 'blur' }
        ],
        decimalPlace: [
          { required: true, message: 'Обязательное поле', trigger: 'blur' },
          { max: 1, message: 'Максимальная длина 1 символ', trigger: 'blur' }
        ],
        value: [
          { required: true, message: 'Обязательное поле', trigger: 'blur' }
        ]
      })
    }
  },

  props: {
    modelValue: Object as PropType<ICurrencyForm>,

    isLoading: Boolean as PropType<boolean>,

    isNew: {
      type: Boolean,
      default: false
    }
  },

  watch: {
    modelValue: {
      handler: function (value) {
        this.model = value
      },
      deep: true
    },

    model: {
      handler: function (value) {
        this.$emit('update:modelValue', value)
      },
      deep: true
    }
  },

  data (): IData {
    return {
      model: this.modelValue || {
        name: '',
        code: '',
        symbolLeft: '',
        symbolRight: '',
        decimalPlace: '',
        value: '0.00000000',
        isActive: false
      },

      isValid: false
    }
  },

  methods: {
    async validate (): Promise<boolean> {
      let isValid = false

      if (this.form) {
        this.form.validate((valid) => {
          if (valid) {
            isValid = true
          }
        })
      }

      return isValid
    }
  }
})
