
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
import useFetching, { FetchStatus, ResponseFetching } from '@/hooks/useFetching'
import { ICurrencyForm } from '@/types'
import PageCard from '@/components/PageCard.vue'
import CurrencyForm from '@/views/system/localization/currency/forms/CurrencyForm.vue'

export interface IData {
  model: ICurrencyForm | null;
}

export default defineComponent({
  setup () {
    const store = useStore()

    const [saveForm, saveStatus, saveError] = useFetching((dataForm: ICurrencyForm) => {
      return store.dispatch('currency/createForm', dataForm)
    }) as ResponseFetching<Promise<ICurrencyForm>>

    const form = ref()

    return {
      saveForm,
      saveStatus,
      saveError,
      form
    }
  },

  components: {
    PageCard,
    CurrencyForm
  },

  computed: {
    isLoading (): boolean {
      return this.saveStatus === FetchStatus.pending
    }
  },

  data (): IData {
    return {
      model: {
        name: '',
        code: '',
        symbolLeft: '',
        symbolRight: '',
        decimalPlace: '',
        value: '0.00000000',
        isActive: false
      }
    }
  },

  methods: {
    handleBack () {
      this.$router.push({ name: 'currency' })
    },

    async handleSubmit () {
      // if (this.form && await this.form.validate()) {
      // }
      const data: ICurrencyForm = await this.saveForm(this.model)
      if (typeof data !== 'undefined') {
        this.model = data
      }

      if (this.model?.id) {
        this.$router.push({ name: 'currency-update', params: { id: this.model.id } })
      }
    }
  }
})
